<template>
  <div>
    <el-form v-model="kechengform" :inline="true" style="margin-top:20px;">
      <el-row style="margin-left:20px;">
        <el-col :span="12">
          <el-form-item label="请输入筛选条件：">
            <el-input
              v-model="input_string"
              placeholder="输入课程ID"
              style="width: 420px;"
            >
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8" style="text-align: right;padding-right:10px;">
          <el-button-group>
            <el-button
              type="primary"
              icon="el-icon-search"
              @click="querykecheng()"
              >筛选</el-button
            >
            <el-button
              type="primary"
              icon="el-icon-tickets"
              @click="getkecheng2()"
              >全部</el-button
            >
          </el-button-group>
        </el-col>
      </el-row>
    </el-form>
    <el-table
      :data="pagekecheng"
      height="400"
      border
      style="width: 90%;margin-left:20px"
      size="mini"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection"> </el-table-column>
      <el-table-column
        type="index"
        label="序号"
        width="60"
        align="center"
        fixed="left"
      >
      </el-table-column>
      <el-table-column prop="ID" label="课程ID" width="120" align="center">
      </el-table-column>
      <el-table-column prop="Name" label="课程名" width="240" align="center">
      </el-table-column>
      <el-table-column
        prop="TimeBegin"
        label="开始时间"
        width="140"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="TimeEnd"
        label="结束时间"
        width="140"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="ID_Location"
        label="地点ID"
        width="140"
        align="center"
      >
      </el-table-column>
      <el-table-column width="400px" fixed="right" label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            type="success"
            icon="el-icon-more"
            size="mini"
            circle
            @click="viewkecheng(scope.row)"
          ></el-button>
          <el-button
            type="info"
            icon="el-icon-view"
            size="mini"
            circle
            @click="handleclick(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-row style="margin-top: 10px;">
      <el-col :span="16" style="text-align: right">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentpage"
          :page-sizes="[5, 10, 25, 50, 100]"
          :page-size="pagesize"
          layout="total,sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </el-col>
    </el-row>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      width="50%"
      @close="closeDialogForm('kechengform')"
    >
      <el-form
        :model="kechengform"
        :rules="rules"
        ref="kechengform"
        :inline="true"
        style="margin-left:20px;"
        label-width="110px"
        label-position="right"
        size="mini"
      >
        <el-form-item
          label="课程ID"
          prop="ID"
          v-if="viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.ID"
            :disabled="isEdit || isView"
            suffix-icon="el-icon-edit"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="备注"
          prop="Rem"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.Rem"
            maxlength="64"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="备注为空填0"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="简介"
          prop="Introduction"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.Introduction"
            maxlength="64"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="简介为空填0"
          ></el-input>
          </el-form-item>
        <el-form-item
          label="课程名"
          prop="Name"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.Name"
            :disabled="isView"
            suffix-icon="el-icon-edit"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="地点ID"
          prop="ID_Location"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.ID_Location"
            :disabled="isView"
            suffix-icon="el-icon-edit"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="主讲人ID"
          prop="ID_Speaker"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.ID_Speaker"
            :disabled="isView"
            suffix-icon="el-icon-edit"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="开始时间"
          prop="TimeBegin"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.TimeBegin"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="00:00:00"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="结束时间"
          prop="TimeEnd"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.TimeEnd"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="00:00:00"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="考勤类型"
          prop="Attr"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-select v-model="kechengform.Attr" :disabled="isView">
            <el-option label="实验类型" value="1"></el-option>
            <el-option label="普通上课类型" value="2"></el-option>
            <el-option label="讲座考勤类型" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="是否收费"
          prop="Charge"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-select v-model="kechengform.Charge" :disabled="isView">
            <el-option label="免费" value="0"></el-option>
            <el-option label="收费" value="1"></el-option>
            <el-option label="开放" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="派位"
          prop="PwAccess"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-select v-model="kechengform.PwAccess" :disabled="isView">
            <el-option label="不派位" value="0"></el-option>
            <el-option label="刷卡派位" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="派位连续性"
          prop="PwContinuous"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-select v-model="kechengform.PwContinuous" :disabled="isView">
            <el-option label="连续派位" value="0"></el-option>
            <el-option label="随机派位" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="派位顺序"
          prop="PwDirection"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-select v-model="kechengform.PwDirection" :disabled="isView">
            <el-option label="顺序派位" value="0"></el-option>
            <el-option label="逆序派位" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="是否开门"
          prop="DoorOpen"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-select v-model="kechengform.DoorOpen" :disabled="isView">
            <el-option label="开门" value="0"></el-option>
            <el-option label="不开门" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="考勤开始最早时间"
          prop="TimeBeginCheckBegin"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.TimeBeginCheckBegin"
            :disabled="isView"
            suffix-icon="el-icon-edit"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="考勤开始最迟时间"
          prop="TimeBeginCheckEnd"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.TimeBeginCheckEnd"
            :disabled="isView"
            suffix-icon="el-icon-edit"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="考勤结束最早时间"
          prop="TimeEndCheckBegin"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.TimeEndCheckBegin"
            :disabled="isView"
            suffix-icon="el-icon-edit"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="考勤结束最迟时间"
          prop="TimeEndCheckEnd"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.TimeEndCheckEnd"
            :disabled="isView"
            suffix-icon="el-icon-edit"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="参与学生学号"
          prop="RangeUsers"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.RangeUsers"
            maxlength="1024"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="学生学号为空填0"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="部门列表"
          prop="ListDepts"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.ListDepts"
            maxlength="1024"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="部门列表为空填0"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="座位的范围表"
          prop="RangeEqus"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.RangeEqus"
            maxlength="1024"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="座位范围表为空填0"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="地点列表"
          prop="ListPlaces"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.ListPlaces"
            maxlength="1024"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="地点列表为空填0"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="学生和座位对应表"
          prop="MapUser2Equ"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.MapUser2Equ"
            maxlength="1024"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="学生和座位对应表为空填0"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="主讲人信息"
          prop="AboutSpeaker"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.AboutSpeaker"
            maxlength="1024"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="主讲人信息为空填0"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="职工号"
          prop="ID_Speaker_NoUser"
          v-if="adddialog || viewdialog || updatedialog || deletedialog"
        >
          <el-input
            v-model="kechengform.ID_Speaker_NoUser"
            :disabled="isView"
            suffix-icon="el-icon-edit"
            placeholder="职工号为空填0"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          size="mini"
          v-show="!isView"
          @click="submitkechengform('kechengform')"
          >确定</el-button
        >
        <el-button
          type="info"
          size="mini"
          @click="closeDialogForm('kechengform')"
          >取消</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
import axios from "axios";
import { errcatch } from "../../errcatch";
import { changetime1, changetime2} from "../../timeset";
export default {
  data() {
    // 校验设备id是否存在
    const rulesID = (rule, value, callback) => {
      if (this.isEdit) {
        callback();
      }
      //使用axios进行校验
      axios
        .post(this.baseURL + "model_courseplan/", {
          ID: value,
          kind: 7
        })
        .then(res => {
          //请求成功
          if (true) {
            if (res.data.exists) {
              callback(new Error("课程ID已存在！"));
            } else {
              callback();
            }
          } else {
            callback(new Error("检验课程ID后端出现异常！"));
          }
        })
        .catch(err => {
          //请求失败打印
          errcatch(err);
        });
    };

    return {
      baseURL: "/api/",
      webtoken:localStorage.getItem("token"),
      kecheng: [], //所有的设备信息
      pagekecheng: [], //分页后当前页的设备信息
      input_string: "", //输入的筛选条件
      //分页的相关参数
      total: 0, //数据的总行数
      currentpage: 1, //当前的所在页
      pagesize: 10, //每行显示多少页
      dialogVisible: false,
      kechengform: {
        ID: "",
        Rem: "",
        Introduction: "",
        Name:"",
        ID_Location: "",
        ID_Speaker: "",
        TimeBegin: "",
        TimeEnd: "",
        Attr: "",
        Charge: "",
        PwAccess: "",
        PwContinuous: "",
        PwDirection: "",
        DoorOpen: "",
        TimeBeginCheckBegin: "",
        TimeBeginCheckEnd: "",
        TimeEndCheckBegin: "",
        TimeEndCheckEnd: "",
        RangeUsers: "",
        ListDepts: "",
        RangeEqus: "",
        ListPlaces: "",
        MapUser2Equ: "",
        AboutSpeaker: "",
        ID_Speaker_NoUser: ""
      },
      searchform:{
    "requires": {},
        service_type: 0,
        page: 1,
        size: 5
},

      dialogTitle: "", //弹出框的标题

      isView: false, //标识是否是查看
      isEdit: false, //标识是否是修改
      adddialog: false,
      viewdialog: false,
      updatedialog: false,
      deletedialog: false,

      selectkechengs: [], //选择复选时把选择记录存在这里
    };
  },
  mounted() {
    //自动加载数据
    this.getkecheng();
  },
  methods: {
    timeup() {},
    datatrans1(row) {
      if (row.Attr == 1) {
        row.Attr = "实验类型";
      } else if (row.Attr == 2) {
        row.Attr = "普通上课类型";
      } else {
        row.Attr = "讲座考勤类型";
      }

      if (row.Charge == 0) {
        row.Charge = "免费";
      } else if (row.Charge == 1) {
        row.Charge = "收费";
      } else {
        row.Charge = "开放";
      }

      if (row.PwAccess == 0) {
        row.PwAccess = "不派位";
      } else {
        row.PwAccess = "刷卡派位";
      }

      if (row.PwContinuous == 0) {
        row.PwContinuous = "连续派位";
      } else {
        row.PwContinuous = "随机派位";
      }
      if (row.PwDirection == 0) {
        row.PwDirection = "顺序派位";
      } else {
        row.PwDirection = "逆序派位";
      }

      if (row.DoorOpen == 0) {
        row.DoorOpen = "开门";
      } else {
        row.DoorOpen = "不开门";
      }
    },
    datatrans2() {
      if (this.kechengform.Attr == "实验类型") {
        this.kechengform.Attr = 0;
      } else if (this.kechengform.Attr == "普通上课类型") {
        this.kechengform.Attr = 1;
      } else {
        this.kechengform.Attr = 2;
      }

      if (this.kechengform.Charge == "免费") {
        this.kechengform.Charge = 0;
      } else if (this.kechengform.Charge == "收费") {
        this.kechengform.Charge = 1;
      } else {
        this.kechengform.Charge = 3;
      }

      if (this.kechengform.PwAccess == "不派位") {
        this.kechengform.PwAccess = 0;
      } else {
        this.kechengform.PwAccess = 1;
      }

      if (this.kechengform.PwContinuous == "连续派位") {
        this.kechengform.PwContinuous = 0;
      } else {
        this.kechengform.PwContinuous = 1;
      }
      if (this.kechengform.PwDirection == "顺序派位") {
        this.kechengform.PwDirection = 0;
      } else {
        this.kechengform.PwDirection = 1;
      }

      if (this.kechengform.DoorOpen == "开门") {
        this.kechengform.DoorOpen = 0;
      } else {
        this.kechengform.DoorOpen = 1;
      }
    },
    //详情生成该课程签到记录表
    handleclick(row) {
      localStorage.setItem("kecheng_ID",row.ID)
      this.$router.push({ path: "/courseplanst"});
    },
    getkecheng2(){
      this.searchform.service_type=0;
      this.getkecheng()
     },
    //获取所有设备信息
    getkecheng: function() {
      //记录this的地址
      let that = this;
      this.searchform.size=this.pagesize;
      this.searchform.page=this.currentpage;
      //使用Axios实现Ajax请求
      axios
        ({url:"/api/" + "model_curricula/search",
        method:'post',
        headers:{
        'accept': "application/json",
        'Authorization':'Bearer'+" "+this.webtoken
      },
      data:this.searchform,
      })
        .then(function(res) {
          //请求成功后执行的函数
          //console.log(res);
          if (res.data.code == 0) {
            that.$router.push("/index");
          } else {
            if (true) {
              //res.data.code ===1
              //把数据给课程
              that.kecheng = res.data.items;
              //获取返回记录的总行数
              that.total = res.data.total;
              //获取当前页的数据
              that.getpagekecheng();
              //数据加载成功提示
              that.$message({
                message: "数据加载成功！",
                type: "success"
              });
            } else {
              //数据加载失败提示
            }
          }
        })
        .catch(err=> {
          //请求失败后执行的函数
          errcatch(err);
        });
    },
    //获取当前页设备数据
    getpagekecheng() {
      //清空pageskecheng中的数据
      this.pagekecheng = [];
      //获得当前页的数据
      for (
        let i = 0;
        i < this.pagesize;
        i++
      ) {
        //遍历数据添加到pageshebei中
        this.kecheng[i].TimeBegin=changetime2(this.kecheng[i].TimeBegin)
        this.kecheng[i].TimeEnd=changetime2(this.kecheng[i].TimeEnd)
        this.pagekecheng.push(this.kecheng[i]);
      }
    },
    //实现当前页的设备信息筛选
    querykecheng() {
      //使用Ajax请求--POST-->传递input_string
      let that = this;
      this.searchform.service_type=3;
      this.searchform['requires'].ID=this.input_string*1;
      //开始Ajax请求
      axios //Axios请求
        ({url:"/api/" + "model_curricula/search",
        method:'post',
        headers:{
        'accept': "application/json",
        'Authorization':'Bearer'+" "+this.webtoken
      },
      data:this.searchform,
      })
        .then(function(res) {
          if (true) {
            //把数据给shebei
            //that.kecheng = res.data.data;
            //获取返回记录的总行数
            //that.total = res.data.data.length;
            //获取当前页的数据
            //that.getpagekecheng();
            that.getkecheng();
            //数据加载成功提示
            that.$message({
              message: "筛选数据加载成功！",
              type: "success"
            });
          } else {
            //数据加载失败提示
            that.$message.error(res.data.msg);
          }
        })
        .catch(function(err) {
          errcatch(err);
        });
    },
    //分页时修改每页的行数
    handleSizeChange(pageNumber) {
      //修改当前每页数据行数
      this.pagesize = pageNumber;
      //数据重新分页
      this.getkecheng();
    },
    //调整当前的页码
    handleCurrentChange(pageNumber) {
      //修改当前的页码
      this.currentpage = pageNumber;
      //数据重新分页
      this.getkecheng();
    },
    //选择复选框触发操作
    handleSelectionChange(data) {
      this.selectkechengs = data;
    },
    //关闭弹出框的表单
    closeDialogForm(formName) {
      //清空数据
      this.kechengform.ID = "";
      this.kechengform.Rem = "";
      this.kechengform.Introduction = "";
      this.kechengform.ID_Location = "";
      this.kechengform.ID_Speaker = "";
      this.kechengform.TimeBegin = "";
      this.kechengform.TimeEnd = "";
      this.kechengform.Attr = "";
      this.kechengform.Charge = "";
      this.kechengform.Name = "";
      this.kechengform.PwAccess = "";
      this.kechengform.PwContinuous = "";
      this.kechengform.PwDirection = "";
      this.kechengform.DoorOpen = "";
      this.kechengform.TimeBeginCheckBegin = "";
      this.kechengform.TimeBeginCheckEnd = "";
      this.kechengform.TimeEndCheckBegin = "";
      this.kechengform.TimeEndCheckEnd = "";
      this.kechengform.RangeUsers = "";
      this.kechengform.ListDepts = "";
      this.kechengform.RangeEqus = "";
      this.kechengform.ListPlaces = "";
      this.kechengform.MapUser2Equ = "";
      this.kechengform.AboutSpeaker = "";
      this.kechengform.ID_Speaker_NoUser = "";
      this.adddialog = false;
      this.adddialog = false;
      this.viewdialog = false;
      this.updatedialog = false;
      this.deletedialog = false;

      //重置表单的校验
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
      });
      //关闭
      this.dialogVisible = false;
      this.isView = false;
      this.isEdit = false;
      this.adddialog = false;
      this.viewdialog = false;
      this.updatedialog = false;
      this.deletedialog = false;
    },
  }
};
</script>
<style>
html,
body,
.el-container {
  margin: 0px;
  padding: 0px;
  height: 100%;
}

.el-dialog .avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.el-dialog .avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.el-dialog .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.el-dialog .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>



